
.painel-jogador {
    background-size: cover;
    border-radius: 10px;
    box-shadow:  5px 5px 8px #464848, 
               -5px -5px 8px #f0f6fa;
    filter: grayscale(100%);
    cursor: pointer;
  }

.painel-jogador-selecionado {
    background-size: cover;
    border-radius: 10px;
    box-shadow: inset 5px 5px 8px #464848, 
            inset -5px -5px 8px #f0f6fa;
            cursor: pointer;
}

  
  .painel-jogadores {
    display: flex;
    flex-direction: column;
    height: 80vh;
    width: 30vw;
    border-radius: 7px;
    background: #9b9fa1;
    box-shadow:  5px 5px 8px #3e4040, 
                 -5px -5px 8px #f8feff;
  }