.jogador-wrapper {
    width: 200px;
    height: 300px;
    background-size: cover;
    background-color: azure;
    box-shadow: 0px 5px 20px 0px black;
}

.jogador-posterior {
  transform:
    rotateY(60deg)
    translateZ(-66px)
    scale(0.95);
}

.jogador-anterior {
    transform:
        rotateY(120deg)
        rotateY(180deg)
      translateZ(-66px)
      scale(0.95);
}

.roleta-wrapper {
    margin: auto;
    perspective: 1000px;
    perspective-origin: 50% 50%;
    height: fit-content;
    
}

.moldura {
    height: 400px;
  width: 400px;
  border-width: 50px;
  border-style:  solid;
  border-radius: 50%;
  position: absolute;
  top: -90px;
  left: 50px;
  z-index: 10;    
  box-shadow:    
  0px 0px 30px 3px rgb(150, 0, 0),
  inset 0px 0px 5px 3px rgb(150, 0, 0),
  10px 10px 10px #3e4040, 
  -10px -10px 10px #f8feff;
}

.fundo {
    border-radius: 50%;
    height: 500px;
  width: 500px;
  position: absolute;
  top: -90px;
  left: 50px;
  background: gray;
  background-size: contain;
}

.roleta {
    display: flex;
    transform-style: preserve-3d;
    transition: transform 6s;
    height: fit-content;
}

