.App {
  text-align: center;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-image: url("./imgs/backgroundNave.jpg");
  background-size: cover;
}

.alarme-ligado {
  filter: grayscale(100%) brightness(40%) sepia(100%) hue-rotate(-50deg) saturate(600%) contrast(0.8);
}

.content-wrapper {
  margin: auto;
  display: flex;
}

.janela-wrapper {
  margin: auto;
  overflow: hidden;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 20px solid #9b9fa1;
  background-image: url('./imgs/background.jpg');
  background-size: cover;
  box-shadow:  10px 10px 10px #3e4040, 
              -10px -10px 10px #f8feff;
}

.painel-jogadores-imagens {
  margin: auto;
  display: grid;
  grid-template-columns: 80px 80px 80px 80px;
  grid-template-rows: 100px 100px 100px 100px;

  column-gap: 5px;
  row-gap: 5px;

}


.painel-botao {
  height: 60px;
  width: 100px;
  line-height: 55px;
  font-size: 24px;
  text-transform: uppercase;
  font-weight: bold;
  color: white;
  cursor: pointer;
  margin: auto;
  border-radius: 17px;
  background: #9b9fa1;
  box-shadow:  5px 5px 7px #3e4040, 
             -5px -5px 7px #f8feff;
}

.painel-botao:active {
  border-radius: 17px;
background: #9b9fa1;
box-shadow: inset 5px 5px 7px #3e4040, 
            inset -5px -5px 7px #f8feff;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.bola {  
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: url("./imgs/tennisball.png") no-repeat;
  background-size: 100%;
  position: absolute;
  animation-name: floating; 
  animation-duration: 100s; 
  animation-iteration-count: infinite; 
  animation-timing-function: ease-in-out; 
  margin-left: 30px; 
  margin-top: 5px; 
} 

.astronauta {  
  width: 50px;
  height: 75px;
  background: url('./imgs/astronauta.png') no-repeat;
  background-size: contain;
  animation-name: floating-astronauta; 
  animation-duration: 50s; 
  animation-iteration-count: infinite; 
  animation-timing-function: ease-in-out; 
  margin-left: 30px; 
  margin-top: 5px; 
  filter: grayscale(50%);
} 

@keyframes floating { 
  0% { 
    transform: 
      translate(calc(0+200px),  0px)
      rotate(0deg);
  } 
  15% { 
    transform: 
      translate(15vw,  calc(100vh - 220px))
      rotate(360deg);
  } 
  30%  { 
    transform: 
      translate(30vw, 0)
      rotate(0deg);
    } 
  45%   { 
    transform:
      translate(calc(100vw - 250px), calc(100vh - 220px))  
      rotate(360deg);
    }    
  60%  { 
    transform:
      translate(60vw, 0)
      rotate(0deg);
  } 
  80% { 
    transform:
      translate(15vw,  calc(100vh - 220px))
      rotate(360deg);
     } 
  100% {
    transform:
      translate(calc(0+200px),  0px)
      rotate(180deg);
   } 
}

@keyframes floating-astronauta { 
  0% { 
    transform: 
      translate(-200px,  0px)
      rotate(0deg);
  } 
  20% { 
    transform: 
      translate(255px, 58px)
      rotate(180deg);
  } 
  40% { 
    transform: 
      translate(-200px, 200px)
      rotate(360deg);
  } 
  60% { 
    transform: 
      translate(180px, -100px)
      rotate(180deg);
  } 
  80% { 
    transform: 
      translate(70px, 250px)
      rotate(360deg);
  } 
  100% { 
    transform: 
      translate(-100px, -200px)
      rotate(360deg);
  } 
}

.bola:before {
  content: "";
  position: absolute;
  top: 1%;
  left: 5%;
  width: 90%;
  height: 90%;
  border-radius: 50%;
  background: radial-gradient(circle at 50% 0px, #ffffff, rgba(255, 255, 255, 0) 58%);
  -webkit-filter: blur(5px);
  z-index: 2;
}
.bola:after {
  content: "";
  position: absolute;
  border-radius: 100%;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: radial-gradient(circle at 50% 30%, rgba(245, 237, 48, 0), rgba(200, 190, 20, 0.2) 50%, #575300 100%);
}
.bola .shadow {
    position: absolute;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.1) 40%, rgba(0, 0, 0, 0) 50%);
    -webkit-transform: rotateX(90deg) translateZ(-160px);
    transform: rotateX(90deg) translateZ(-160px);
    z-index: 1; }


.rodando {
  animation: spin 0.7s infinite linear;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}